p {
  line-height: 1.75rem;
  font-size: 1.15rem;
}
a,
a:link,
a:visited,
a:hover,
a:active {
  color: #776e65;
  font-weight: bold;
  text-decoration: underline;
  cursor: pointer;
}
hr {
  border: 0;
  width: 100%;
  background-color: #d8d4d0;
  height: 1px;
}
.game_container {
  width: 100%;
  height: auto;
  font-family: "Clear Sans";
  padding-top: 5vh;
}
.top_container {
  margin: 0 auto;
  width: 515px;
  height: auto;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  align-content: center;
}
.btc_container {
  display: flex;
  align-items: center;
}
.btc_img {
  display: block;
  width: 64px;
  height: 64px;
  background: url(bitcoin.svg);
  background-size: 64px 64px;
  margin: 5px 5px 5px 0;
}
.btc_container p {
  margin: 0;
  float: left;
  font-size: 56px;
  font-weight: bold;
}
.score_container {
  position: relative;
  display: inline-block;
  background: #bbada0;
  padding: 15px 25px;
  font-size: 25px;
  height: 25px;
  line-height: 47px;
  font-weight: bold;
  border-radius: 3px;
  text-align: center;
}
.score_container p:nth-of-type(1) {
  font-size: 25px;
  line-height: 25px;
  color: #eee4da;
  font-size: 13px;
  line-height: 13px;
  margin: 0;
  position: absolute;
  width: 100%;
  top: 10px;
  left: 0;
}
.score_container p:nth-of-type(2) {
  font-size: 25px;
  line-height: 25px;
  font-weight: bold;
  font-size: 25px;
  line-height: 47px;
  margin: 0;
  color: white;
}
.intro {
  margin: 20px 0 0;
}
.reset_button {
  margin: 20px 0 0;
  font-weight: bold;
  background: #8f7a66;
  border-radius: 3px;
  padding: 5px 20px;
  text-decoration: none;
  color: #f9f6f2;
  height: 40px;
  line-height: 40px;
  display: block;
  text-align: center;
}

.middle_container {
  position: relative;
  width: 515px;
  height: 515px;
  margin: 30px auto 30px;
  padding: 0;
}
.modal_container {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 6px;
  background: rgba(5, 5, 5, 0.3);
}
.modal_message {
  text-align: center;
  vertical-align: middle;
  line-height: 430px;
  font-size: 2.5rem;
  font-weight: bold;
  color: #f9f6f2;
}
.game_board {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background: #bbada0;
  border-radius: 6px;
  border-spacing: 15px;
}
.game_board td {
  padding: 0;
}
.cell {
  width: 110px;
  height: 110px;
  border-radius: 3px;
  background: rgba(238, 228, 218, 0.35);
  font-size: 55px;
  font-weight: bold;
}
.tile {
  position: relative;
  width: 110px;
  height: 110px;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.color_2 {
  background: #eee4da;
  box-shadow: 0 0 30px 10px rgba(243, 215, 116, 0),
    inset 0 0 0 1px rgba(255, 255, 255, 0);
}
.color_4 {
  background: #ede0c8;
  box-shadow: 0 0 30px 10px rgba(243, 215, 116, 0),
    inset 0 0 0 1px rgba(255, 255, 255, 0);
}
.color_8 {
  color: #f9f6f2;
  background: #f2b179;
}
.color_16 {
  color: #f9f6f2;
  background: #f59563;
}
.color_32 {
  color: #f9f6f2;
  background: #f67c5f;
}
.color_64 {
  color: #f9f6f2;
  background: #f65e3b;
}
.color_128 {
  color: #f9f6f2;
  background: #edcf72;
  box-shadow: 0 0 30px 10px rgba(243, 215, 116, 0.2381),
    inset 0 0 0 1px rgba(255, 255, 255, 0.14286);
  font-size: 45px;
}
.color_256 {
  color: #f9f6f2;
  background: #edcc61;
  box-shadow: 0 0 30px 10px rgba(243, 215, 116, 0.31746),
    inset 0 0 0 1px rgba(255, 255, 255, 0.19048);
  font-size: 45px;
}
.color_512 {
  color: #f9f6f2;
  background: #edc850;
  box-shadow: 0 0 30px 10px rgba(243, 215, 116, 0.39683),
    inset 0 0 0 1px rgba(255, 255, 255, 0.2381);
  font-size: 45px;
}
.color_1024 {
  color: #f9f6f2;
  background: #edc53f;
  box-shadow: 0 0 30px 10px rgba(243, 215, 116, 0.47619),
    inset 0 0 0 1px rgba(255, 255, 255, 0.28571);
  font-size: 35px;
}
.color_2048 {
  color: #f9f6f2;
  background: #edc22e;
  box-shadow: 0 0 30px 10px rgba(243, 215, 116, 0.55556),
    inset 0 0 0 1px rgba(255, 255, 255, 0.33333);
  font-size: 35px;
}
.color_super {
  color: #f9f6f2;
  background: #3c3a32;
  font-size: 30px;
}
.matched {
  animation: pop 200ms ease 100ms;
}
.bottom_container {
  margin: 0 auto;
  width: 515px;
  height: auto;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
}

@keyframes pop {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.new {
  animation: appear 200ms ease 100ms backwards;
}
@keyframes appear {
  0% {
    opacity: 0;
    transform: scale(0);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@media only screen and (min-width: 800px) {
  .game_container {
    width: 800px;
    margin: auto;
  }
}
@media only screen and (max-width: 515px) {
  p {
    line-height: 1.3rem;
    font-size: 1rem;
  }
  .top_container {
    width: 280px;
  }
  .btc_img {
    width: 48px;
    height: 48px;
    background-size: 48px 48px;
  }
  .btc_container {
    width: 70%;
  }
  .btc_container p {
    font-size: 28px;
  }
  .score_container {
    /* width: 40%; */
  }
  .intro {
    width: 70%;
  }
  .reset_button {
    width: 15%;
  }

  .middle_container {
    width: 280px;
    height: 280px;
  }
  .modal_message {
    line-height: 275px;
    font-size: 2rem;
  }
  .game_board {
    border-radius: 3px;
    border-spacing: 10px;
  }
  .cell {
    width: 58px;
    height: 58px;
    font-size: 35px;
  }
  .tile {
    width: 58px;
    height: 58px;
  }
  .color_128,
  .color_256,
  .color_512 {
    font-size: 28px;
  }
  .color_1024,
  .color_2048 {
    font-size: 18px;
  }
  .color_super {
    font-size: 15px;
  }

  .bottom_container {
    width: 280px;
  }
}
